<template lang="html">
  <div class="ad-rate-cash">
    <!-- 头部 -->
    <default-head header="广告费余额提现"></default-head>
    <light-title text="请确认是持卡人银行卡"></light-title>
    <form class="form-box">
      <form-line-light label="到账银行卡" :disabled="true" v-model="bankMsg" width="2.32rem" class="borderB"></form-line-light>
      <input type="hidden" v-model="otherInfo.realName" data-vv-name="用户名" v-validate="'required'" name="realName">
      <input type="hidden" v-model="otherInfo.cardNo" data-vv-name="银行卡号" v-validate="'required'" name="cardNo">

      <form-line-light label="提现金额" v-model="userInfo.amount" data-vv-name="提现金额" v-validate="'required'" name="cardNo"
      placeholder="提现金额" paddingLeft=".3rem" color="red" width="2.32rem" class="borderB">
        <i class="rmb-icon">¥</i>
      </form-line-light>
      <form-line-light label="输入支付密码" inpuType="password" v-model="userInfo.payPwd" data-vv-name="支付密码" v-validate="'required'" name="payPwd"
      width="2.32rem" placeholder="支付密码"></form-line-light>

      <p class="agree fir-agree"><b>广告费余额:</b>  ¥ {{parseInt(otherInfo.adFeeValue).toFixed(2)}}</p>
      <p class="agree"><b>提示:</b>  <span>提现单笔金额需满100元人名币, 手续费为5%, 单个用户单日提现上限为5万, 单月上限为20万</span></p>

      <sub-button text="提现" @click.native="sumitRateCash"></sub-button>
      <p class="warn">当天24小时内到账</p>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import formLine from '@/components/common/formLine'
import formLineLight from '@/components/common/formLineLight'
import subButton from '@/components/common/subButton'

export default {
  data () {
    return {
      userInfo: {
        userId: 0,
        amount: 0,
        payPwd: '',
        status: 0,
        userType: utils.getType()
      },
      otherInfo: {
        realName: decodeURI(this.$route.query.userName),
        cardNo: '',
        adFeeValue: 0
      }
    }
  },
  components: { defaultHead, lightTitle, formLine, formLineLight, subButton },
  created () {
    this.initPage()
  },
  computed: {
    bankMsg () {
      let cardNo = this.otherInfo.cardNo
      return this.otherInfo.realName + '银行卡(' + cardNo.substring(cardNo.length - 4, cardNo.length) + ')'
    },
    userId () {
      return utils.userId()
    },
    userType () {
      return utils.getType()
    }
  },
  methods: {
    initPage () {
      // 初始化银行卡列表
      this.ajaxVerifyPayPassword({userId: this.userId})
    },
    sumitRateCash () {
      this.$validator.validateAll().then(() => {
        if (parseInt(this.userInfo.amount) < 100) {
          return minutils.Toast({ message: '提现金额不能少于100元人民币', position: 'top' })
        } else if (this.userInfo.amount > parseInt(this.$route.query.cash)) {
          return minutils.Toast({ message: '当前账号余额不足', position: 'top' })
        }

        this.ajaxApplyWithdrawals(this.userInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    isBindCard () {
      let self = this
      minutils.MessageBoxConfirm({
        message: '您暂未绑定银行卡, 请绑定后再提现！',
        options: {confirmButtonText: '立即设置', cancelButtonText: '取消设置'},
        success () {
          self.$router.push({path: 'bindbankcard', query: {realName: self.$route.query.userName}})
        },
        fail (action) {
          self.$router.go(-1)
        }
      })
    },
    hasPayPwd (path) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '您暂未设置支付密码, 请设置支付密码后充值',
        options: {confirmButtonText: '立即设置', cancelButtonText: '取消设置'},
        success (action) {
          self.$router.push(path)
        },
        fail (action) {
          self.$router.go(-1)
        }
      })
    },
    ajaxGetUserBankCard (data) {
      api.getUserBankCard(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.otherInfo.cardNo = result.data[0].cardNo || ''
          this.userInfo.userId = result.data[0].userId || ''
          // this.userInfo.status = result.data[0].status || 0
          // if (this.userInfo.status === 0) {
          //   this.isBindCard()
          // }
        }
      })
    },
    ajaxGetUserAdFee (data) {
      api.getUserAdFee(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.otherInfo.adFeeValue = result.data.amount
        }
      })
    },
    ajaxVerifyPayPassword (data) {
      api.verifyPayPassword(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          if (!result.data) {
            this.hasPayPwd('/modifypayword')
          } else {
            this.ajaxGetUserBankCard({userId: this.userId, userType: this.userType, ifSuccess: true})
            this.ajaxGetUserAdFee({userId: this.userId})
          }
        }
      })
    },
    ajaxApplyWithdrawals (data) {
      api.applyWithdrawals(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.otherInfo.adFeeValue = this.otherInfo.adFeeValue - this.userInfo.amount
          minutils.MessageBox({message: '您提现的金额将会在24小时之内到账'})
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.ad-rate-cash {
  padding-top: .88rem;
  .form-box {
    // height: 4.7rem;
    // background: @whiteColor;
    .rmb-icon {
      position: absolute;
      left: 0;
      top: 0;
      font-size: .32rem;
      color: @redColor;
    }
    .agree {
      font-size: .3rem;
      color: @defaultColor;
      padding-left: .22rem;
      padding-top: .2rem;
      margin-bottom: 1rem;
      line-height: .44rem;
      span {
        font-size: .26rem;
        color: @midleColor;
      }
    }
    .fir-agree {
      margin-bottom: 0;
    }
    .warn {
      font-size: .3rem;
      color: @defaultColor;
      padding-top: .2rem;
      text-align: center;
    }
  }
}

</style>



